.advantages{
    padding-bottom: 97px;

    @media screen and (min-width:768px){
    }
   

   background-color: $second-bg-color;
}
.section__title-advantages{
    display: none;
}
.advantages__list{
    @media screen and (min-width:768px){
        display:flex;
        flex-wrap:nowrap;
        margin: -30px;  
    }

    @media screen and (min-width:1280px){
        margin: -15px;  
    }
    
}

.advantages__item{
    width: 100%;
    text-align: left;
    transform: rotate(2160deg) scale(0);
    opacity: 0;
    transition: all 0.4s ease 0s;
    cursor: pointer;
    
 
    
    &._active{
        transform: rotate(0) scale(1);
        opacity: 1;
     
        &:nth-child(2) {
            transition: all 0.4s ease 0.4s;
          }
        
          &:nth-child(3) {
            transition: all 0.4s ease 0.8s;
          }


          &:hover{
            transform: scale(1.2);
            transition: all 0.4s ease 0.2s;
            
            &:nth-child(2) {
                transition: all 0.4s ease 0s;
              }
            
              &:nth-child(3) {
                transition: all 0.4s ease 0s;
              }
        }
        
        &:hover .advantages__title{
            transition: all 0.4s ease 0.4s;
            transform: translate(30%, 0px);  
        }
        
    }


    @media (max-width: 767px){
        width: 230px;
    &:not(:last-child) {
        margin-bottom: 38px;
    }
}
 @media screen and (min-width:768px){
    width: calc((100% - 30px*4)/3);
    margin: 30px;
 }

@media screen and (min-width:1280px){
    width: calc((100% -15*4)/3);
    margin: 15px;
 }
 
 &::before {
    display: block;
    content: '';
    width:33px;
    height: 50px;
    background-image: url(../images/icon1.png);
    // background-size: 33px 50px;
    background-repeat: no-repeat;
    background-position: center;
 }

 &-second::before{
    background-image: url(../images/icon2.png);
    width: 50px;
 }

 &-third::before{
    background-image: url(../images/icon3.png);
    width: 44px;
 }
}

.advantages__title{
font-family: $second-font;
font-weight: normal;
font-size: 36px;
line-height: 1.14;
letter-spacing: 0.04em;
text-transform: uppercase;

color: $text-title-color;

margin-top: 25px;
margin-bottom: 20px;

@media screen and (min-width:768px){
    margin-top: 36px;
    margin-bottom: 13px;
    font-size: 48px;
    line-height: 1.15;
    text-transform: none;
 }

@media screen and (min-width:1280px){
    margin-top: 40px;
    margin-bottom: 15px;
    font-size: 58px;
    line-height: 1.14;
 }
}

.advantages__text{
font-weight: 700;
font-size: 12px;
line-height: 1.7;

color: $main-text-color;

@media screen and (min-width:768px){
    font-size: 14px;
    line-height: 1.74;
 }

@media screen and (min-width:1280px){
    font-size: 16px;
    line-height: 1.76;  
    letter-spacing: 0.02em;
 }
}

#myBtn {
    display: none; /* Hidden by default */
    position: fixed; /* Fixed/sticky position */
    bottom: 20px; /* Place the button at the bottom of the page */
    right: 30px; /* Place the button 30px from the right */
    z-index: 99; /* Make sure it does not overlap */
    border: none; /* Remove borders */
    outline: none; /* Remove outline */
    background-color: $main-hero-bg-color; /* Set a background color */
    color: $button-second-color; /* Text color */
    cursor: pointer; /* Add a mouse pointer on hover */
    padding: 15px; /* Some padding */
    border-radius: 10px; /* Rounded corners */
    font-size: 24px; /* Increase font size */
    font-weight: 700;
}

#myBtn:hover {
    color: $main-bg-color;
    background-color: $text-title-color; /* Add a dark-grey background on hover */
}